html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 1.6rem;
  color: #000;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.contenedor {
  max-width: 90rem;
  width: 90%;
  margin: 0 auto;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.btn {
  background-color: #00272B;
  color: #fff;
  padding: 1.5rem 2.5rem;
  border-radius: 0.8rem;
  cursor: pointer;
  outline: none;
  border: none;
  transition: 0.3s all ease;
}
.btn:hover {
  background-color: #004047;
}

.btn-subrayado {
  color: #000;
  padding: 1.5rem 0;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: 0.1rem solid #000;
  transition: 0.3s all ease;
}
.btn-subrayado:hover {
  color: #242424;
  border-bottom-color: #242424;
}

.conjunto-botones {
  display: flex;
  flex-direction: row;
  gap: 3.5rem;
}
.conjunto-botones i {
  padding: 0;
  margin-left: 1rem;
}

.centrar-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.guia {
  outline: 0.1rem solid red;
}

header {
  background-color: #e0ff4f;
  padding: 5rem 0;
  border-radius: 0 0 5rem 5rem;
}
header div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
header p {
  margin: 0;
  text-align: center;
}
header .disponible {
  font-size: 2.5rem;
}
header .dinero {
  font-weight: 800;
  font-size: 8rem;
}

footer {
  margin-top: 5rem;
  padding: 5rem 0;
  background-color: #e0ff4f;
}
footer p {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  margin: 2rem 0 0 0;
}

.conjunto-iconos-footer {
  border-bottom: 0.1rem solid #bad825;
  width: 90rem;
  padding-bottom: 2rem;
}

.icono {
  font-size: 1.6rem;
  padding: 1rem;
  border-radius: 50%;
  margin: 0;
  cursor: pointer;
  transition: 0.3s ease all;
}

.icono-editar {
  color: #fff;
  background-color: #e0e0e0;
  color: #000;
}
.icono-editar:hover {
  background-color: #b3b3b3;
}

.icono-borrar {
  color: #fff;
  background-color: #f10a0a;
}
.icono-borrar:hover {
  background-color: #db0707;
}

.conjunto-iconos {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.icono-social {
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  background-color: #d5f732;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s all ease;
}
.icono-social:hover {
  background-color: #cae740;
}
.icono-social .icono-footer {
  color: #000;
}

.alerta {
  text-align: center;
  margin: 2.5rem 0 -2.5rem 0;
  padding: 1.5rem;
  border-radius: 0.8rem;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
}

.exito {
  background-color: #31af01;
}

.error {
  background-color: #f10a0a;
}

table {
  margin-top: 5rem;
  margin-bottom: 5rem;
  width: 100%;
  border-radius: 0.8rem;
  overflow: hidden;
}
table th {
  font-size: 1.8rem;
  color: #fff;
}
table .egreso {
  color: #f10a0a;
}
table .ingreso {
  color: #278a01;
}

.t-center {
  text-align: center;
}

.tc-50 {
  width: 50%;
}

table, td, th {
  border-collapse: collapse;
}

td {
  border-right: 0.1rem solid #b3b3b3;
}

th:last-of-type, td:last-of-type {
  border-right: none;
}

table thead {
  text-align: left;
}

th {
  background-color: #00272B;
  color: #fff;
  font-size: 1.8rem;
  padding: 1.5rem 2rem;
}

table tbody tr td {
  padding: 1.5rem 2rem;
}

/* Bg-color gris cada 2 filas */
table tbody tr:nth-of-type(even) {
  background-color: #e0e0e0;
}

/* Borde al ultimo elemento de la tabla */
table tbody tr:last-of-type {
  border-bottom: 0.3rem solid #00272B;
}

.no-registros {
  text-align: center;
  font-weight: 700;
  padding: 2rem 0;
}

table form {
  display: inline;
}

.btn-eliminar {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.form_registro {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: min(60rem, 90%);
  gap: 2rem;
  padding: 5rem;
  margin: 2rem 0;
}

.form__col {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.form__label {
  width: 18%;
  align-self: center;
}

.form__input {
  width: 82%;
}
.form__input input, .form__input select {
  outline: 0.1rem solid #c1c1c1;
  padding: 1.2rem;
  border-radius: 0.5rem;
  border: none;
  transition: 0.3s all ease;
}
.form__input input:focus, .form__input select:focus {
  outline: 0.1rem solid #757575;
}
.form__input input {
  width: 100%;
}
.form__input input::placeholder {
  color: #757575;
}

.form__cantidad {
  position: relative;
}
.form__cantidad .signo_cantidad {
  margin: 0;
  color: #757575;
  position: absolute;
  top: 50%;
  left: 1.2rem;
  transform: translate(-50%, -50%);
}
.form__cantidad #cantidad {
  padding-left: 2rem;
}

.form_actualizar-cantidad {
  width: 50%;
  padding: 0;
}

.form__input-100, .form__label-100 {
  width: 100%;
}

.form__col-margin-top {
  margin-top: 1rem;
}

.p-cantidad-disponible {
  margin: 5rem 0;
  font-size: 1.8rem;
}
.p-cantidad-disponible span {
  font-weight: 700;
}

.form-cantidad {
  padding: 0 5rem;
  margin: 0;
}

.contenedor-error-db {
  height: 100vh;
  justify-content: center;
}
.contenedor-error-db .error-db {
  font-weight: 800;
}